.appointment_type_button_input{
    position: relative;
    margin-bottom: 0;
    margin-top: 20px;
    border-radius: 7px;
    opacity: 1;
   
      .appointment_type_button_input_box{
          text-align: left;
          width: 100%;
          height: 60px;
          background: #F5F5F5;
          border:none;
          font-size: 18px;
          font-weight: normal;
          letter-spacing: 0px;
          color: #162D43;
          opacity: 1;
          padding: 0 20px;
          border-radius: 7px;
          border: 2px solid #F5F5F5;
          &:hover{
            background: #EFF8FF 0% 0% no-repeat padding-box;
            border: 2px solid #208CE1;
            color: #208CE1;
        }
      }
      
}
.appointment_type_main_heading{
    margin-top: 30px;
    margin-bottom: 30px;
    font-size:25px; 
    font-weight: 600;
    letter-spacing: 0px;
    color: #162D43;
    opacity: 1;
  }
@media screen and (max-width:575px) {
  .appointment_type_button_input{
    .appointment_type_button_input_box{
      padding: 0 15px;
      font-size: 17px;
    }
  }
}

 