.emergency_contact_main_heading {
  margin-top: 35.82px;
  margin-bottom: 35px;
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0px;
  color: #162d43;
  opacity: 1;
}
.emergency_contact_label {
  font-weight: normal;
  font-size: 15px;
  letter-spacing: 0px;
  color: #53687b;
  opacity: 1;
  margin-bottom: 4px;
}
.emergency_phoneInpt {
  :global .react-tel-input .form-control {
    position: relative;
    font-size: 14px;
    letter-spacing: 0.01rem;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    padding-left: 48px;
    margin-left: 0;
    background: #ffffff;
    /* border: 1px solid #CACACA; */
    /* border-radius: 5px; */
    line-height: 25px;
    /* height: 35px; */
    width: x;
    outline: none;
    height: 60px;
    width: 100%;
    border: 1px solid #a3c1de;
    border-radius: 7px;
    box-shadow: none;
  }
  :global .react-tel-input .flag-dropdown {
    position: absolute;
    top: 0;
    bottom: 0;
    padding: 0;
    background-color: #fff;
    /* border: 1px solid #cacaca; */
    border-radius: 7px 0 0 7px;
    border: 1px solid #a3c1de;
  }
}
.emergency_contact_country_code {
  max-width: 105px;
  width: 100%;
  height: 60px;
  color: #162d43;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 0.38px;
  border: 1px solid #a3c1de;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  background-size: 12px;
  // border-right: 0;
}
.emergency_contact_input_field {
  border: 1px solid #a3c1de;
  border-radius: 7px;
  padding: 0 16px;
  width: 100%;
  height: 60px;
  background: #ffffff;
  color: #162d43;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 20px;
  &:focus-visible {
    outline: none;
  }
  &::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #5d7082;
    font-weight: 400;
  }
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: #5d7082;
    font-weight: 400;
  }
  &:-ms-input-placeholder {
    /* IE 10+ */
    color: #5d7082;
    font-weight: 400;
  }
  &:-moz-placeholder {
    /* Firefox 18- */
    color: #5d7082;
    font-weight: 400;
  }
}
.emergency_contact_mobile_input_field {
  border: 1px solid #a3c1de;
  border-radius: 0;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  border-left: 0;
  padding: 0 16px;
  width: 100%;
  height: 60px;
  background: #ffffff;
  color: #162d43;
  font-size: 17px;
  font-weight: 500;
  &::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #5d7082;
    font-weight: 400;
  }
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: #5d7082;
    font-weight: 400;
  }
  &:-ms-input-placeholder {
    /* IE 10+ */
    color: #5d7082;
    font-weight: 400;
  }
  &:-moz-placeholder {
    /* Firefox 18- */
    color: #5d7082;
    font-weight: 400;
  }
}
.error {
  color: #ff6262;
  font-size: 14px;
}
.inputError {
  margin-bottom: 5px !important;
  border: 1px solid #ff6262 !important;
  border-radius: 8px;
}
.emergency_contact_frequency_select_field {
  color: #162d43;
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 0px;
  opacity: 1;
  width: 100%;
  height: 60px;
  border: 1px solid #a3c1de;
  border-radius: 7px;
  padding-left: 18px;
  margin-bottom: 20px;
}
.emergency_contact_continue_button {
  width: 100%;
  height: 60px;
  background: #208ce1 0% 0% no-repeat padding-box;
  border-radius: 7px;
  opacity: 1;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: 0px;
  color: #ffffff;
  margin-top: 29.41px;
  border: 1px solid #208ce1 !important;
  &:hover {
    background: transparent;
    color: #208ce1;
  }
}
:global .mob-flag-wrp {
  display: flex;
  margin-bottom: 1rem;
}
@media screen and (max-width: 575px) {
  .emergency_contact_continue_button {
    height: 55px;
    font-size: 17px;
    margin-top: 20px;
  }
}
