.hospital_visit_main_heading{
    margin-top: 35.82px;
    margin-bottom: 35px;
    font-size:25px; 
    font-weight: 600;
    letter-spacing: 0px;
    color: #162D43;
    opacity: 1;
  }
  .hospital_visit_continue_button{
    width: 100%;
    height: 60px;
    background: #208CE1;
    border-radius: 7px;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 0px;
    color: #FFFFFF;
    border: 1px solid #208CE1;
    margin-top: 29.41px;
    &:hover {
      background:transparent;
      color:#208CE1;
    }
  }

  .error{
    color: #ff6262;
    font-size: 14px;
  }
  .inputError{
    margin-bottom: 5px !important;
    border: 1px solid #ff6262 !important;
    border-radius: 8px;
  }

  .hospital_visit_label{
    margin-top: 36.15px;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: 0px;
    color: #53687B;
    text-transform: none;
    opacity: 1;
    margin-bottom: 9px;
  }
  .hospital_visit_input_field{
    font-weight: 500;
    font-size: 17px;
    letter-spacing: 0px;
    color: #162D43;
    opacity: 1;
    width: 100%;
    height: 60px;
    border: 1px solid #A3C1DE;
    border-radius: 7px;
    padding-left: 18px;
    margin-bottom: 5px;
  }
  @media screen and (max-width:575px) {
    .hospital_visit_continue_button{
        height: 55px;
        font-size: 17px;
        margin-top:20px;
      }
    }