.book_appointment_previous_step{
    color:#162D43;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 0px;
    opacity: 1;
    margin-left: 15px;
    cursor: pointer;
  }
  .book_appointment_main_heading{
    margin-top: 30px;
    margin-bottom: 30px;
    font-size:25px; 
    font-weight: 600;
    letter-spacing: 0px;
    color: #162D43;
    opacity: 1;
  }
  .book_appointment_label{
    font-weight: normal;
    font-size: 15px;
    letter-spacing: 0px;
    color: #53687B;
    // text-transform: capitalize;
    opacity: 1;
    margin-bottom: 9px;
    display: block;
  }
  .book_appointment_input_field{
    font-weight: normal;
    font-size: 17px;
    letter-spacing: 0px;
    color: #162D43;
    height: 60px;
    border: 1px solid #A3C1DE;
    border-radius: 7px;
    padding-left: 18px;
    margin-bottom: 20px;
    width: 100%;
    font-weight: 500;
    &::-webkit-input-placeholder { /* Edge */
      color: #5D7082 !important;
    }
    
    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
      color: #5D7082 !important;
    }
    
    &::placeholder {
      color: #5D7082 !important;
    }
    &:focus,&:focus-visible {
      outline: none;
    }
  }

  .book_appointment_confirmation_paragraph{
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0px;
    color: #162D43;
    opacity: 1;
    margin: 10px 0 25px;
    text-align: left;
  }
  .book_appointment_continue_button{
    width: 100%;
    height: 60px;
    background: #208CE1;
    border-radius: 7px;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 0px;
    color: #FFFFFF;
    border: 1px solid #208CE1;
    &:hover {
      background:transparent;
      color:#208CE1;
    }
    &:focus,&:focus-visible {
      box-shadow: none;
    }
  }

  .error{
    color: #ff6262;
    font-size: 14px;
  }
  .inputError{
    margin-bottom: 5px;
    border: 1px solid #ff6262;
  }