.national_id_main_heading {
  margin-top: 35.82px;
  margin-bottom: 35px;
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0px;
  color: #162d43;
  opacity: 1;
}
.helpContent {
  border: 1px solid #d8d8d8;
  padding: 18px 18px 18px 18px;
}
.national_id_label {
  font-weight: normal;
  font-size: 15px;
  letter-spacing: 0px;
  color: #53687b;
  text-transform: capitalize;
  opacity: 1;
  margin-bottom: 9px;
}
.national_id_input_field {
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 0px;
  color: #162d43;
  opacity: 1;
  width: 100%;
  height: 60px;
  border: 1px solid #a3c1de;
  border-radius: 7px;
  padding: 0 18px;
  margin-bottom: 20px;
}
.error {
  color: #ff6262;
  font-size: 14px;
}
.inputError {
  margin-bottom: 5px;
  border: 1px solid #ff6262;
}
