.national_id_upload_button {
  position: relative;
  margin-bottom: 10px;
  border-radius: 7px;
  opacity: 1;
  .national_id_upload_icon_image {
    margin-right: 15px;
  }
  .national_id_upload_button_box {
    text-align: center;
    width: 100%;
    height: 60px;
    background: #f5f5f5;
    border: none;
    font-size: 18px;
    font-weight: normal;
    letter-spacing: 0px;
    opacity: 1;
    padding-right: 70px;
    color: #6f7d8a;
  }
  Button:hover {
    background: #eff8ff;
    border: 2px solid #208ce1;
    opacity: 1;
  }
  .national_id_help_icon {
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0px;
    color: #208ce1;
    position: absolute;
    cursor: pointer;
    top: 19px;
    right: 15px;
    cursor: pointer;
    img {
      margin-left: 3px;
    }
  }
}
.national_id_updated_btn {
  position: relative;
  margin-bottom: 10px;
  border-radius: 7px;
  opacity: 1;
  .national_id_upload_icon_image {
    margin-right: 15px;
  }
  .national_id_upload_button_box {
    text-align: center;
    width: 100%;
    height: 60px;
    cursor: initial;
    background: #eff8ff;
    border: 2px solid #208ce1;
    opacity: 1;
    font-size: 18px;
    font-weight: normal;
    letter-spacing: 0px;

    color: #208ce1;
  }

  .national_id_upload_delete_image {
    cursor: pointer;
  }
}
.national_id_uploaded_button {
  position: relative;
  margin-bottom: 20px;
  margin-top: 20px;
  border-radius: 7px;
  opacity: 1;
  .national_id_tick_icon_image {
    width: 19px;
    height: 19px;
    position: absolute;
    left: 25px;
    top: 20px;
  }
  .national_id_uploaded_button_box {
    text-align: center;
    width: 100%;
    height: 60px;
    background: #eff8ff;
    border: 2px solid #208ce1;
    border-radius: 7px;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0px;
    opacity: 1;
    padding-right: 45px;
    color: #208ce1;
    &:focus {
      box-shadow: none;
    }
  }

  .national_id_close_icon {
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0px;
    color: #208ce1;
    position: absolute;
    right: 15px;
    top: 17px;
    cursor: pointer;
    img {
      margin-left: 3px;
    }
  }
}
@media screen and (max-width: 575px) {
  .national_id_continue_button {
    height: 55px;
    font-size: 17px;
  }
  .national_id_upload_button {
    .national_id_upload_button_box {
      margin-bottom: 15px;
    }
    .national_id_upload_icon_image {
      margin-right: 8px;
    }
  }
}
