.patient_consent_modal_align {
  max-width: 563px;
  width: 100%;
  height: 624px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: -3px 3px 6px #0000001a;
  opacity: 1;
  border: none;
  right: 0;
  margin: auto;
  overflow-y: hidden;
  border-radius: 10px;
  z-index: 10000;
  top: 14%;
  position: absolute !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
  .modal_title {
    padding-bottom: 15px;
  }
  :global .modal-dialog {
    margin: auto;
    margin-right: 0px;
    margin-left: 63px;
  }
  :global .modal-body {
    max-height: 430px;
    overflow-y: auto;
    padding: 0 20px 1rem 0;
    padding-right: 55px;
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: none;
      border-radius: 10px;
      background-color: transparent;
    }

    &::-webkit-scrollbar {
      width: 3px;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: none;
      background-color: #90b7d5;
    }
  }
  :global .modal-content {
    border: 0;
  }
  :global .btn-close {
    background-size: 15px;
    position: relative;
    top: -16px;
    margin-right: 15px;
  }
  .patient_consent_heading {
    text-align: left;
    font-weight: 600;
    font-size: 22px;
    letter-spacing: 0.24px;
    color: #000000;
    opacity: 1;
    border: none;
    padding: 30px 0 0;
  }
  .patient_consent_body {
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0px;
    color: #162d43;
    text-align: justify;
    .header{
      font-weight: 600;
    }
  }
  .patient_consent_accept_button {
    width: 89%;
    height: 60px;
    background: #208ce1;
    border-radius: 7px;
    opacity: 1;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 0px;
    color: #ffffff;
    margin-top: 29.41px;
    border: 1px solid #208ce1;
    &:hover {
      background: transparent;
      color: #208ce1;
    }
  }
}
@media screen and (max-width: 575px) {
  .patient_consent_modal_align {
    top: 0;
    height: 100%;
    max-width: inherit;
    overflow-y: auto;
    border-radius: 0;
    padding: 0;
    :global .modal-body {
      max-height: inherit;
      overflow-y: hidden;
      padding: 1rem 0;
    }
    :global .modal-content {
      padding: 0 15px 25px;
    }
    :global .modal-dialog {
      margin: auto;
      margin-right: 10px;
      margin-left: 10px;
    }
    :global .btn-close {
      background-size: 15px;
      position: relative;
      top: -16px;
      margin-right: 0px;
    }
    .patient_consent_heading {
      padding: 30px 0 0;
    }
  }
}
