.period_accordion_accordion_align {
  background: #eff8ff;
  border-radius: 7px;
  margin-top: 20.43px;
  :global .accordion-item {
    border: 0;
    border-radius: 7px !important;
  }
  :global .accordion-button {
    background: #eff8ff;
    border-radius: 0 !important;
    box-shadow: none;
    height: 60px;
    border-radius: 0 !important;
    border-top-left-radius: 7px !important;
    border-top-right-radius: 7px !important;
    &.collapsed {
      background: #f5f5f5;
      border-radius: 7px !important;
    }
  }
  :global .accordion-collapse {
    background: #eff8ff;
    border-bottom-left-radius: 7px !important;
    border-bottom-right-radius: 7px !important;
  }
  .period_accordion_main_heading {
    // font-weight:500;
    font-size: 18px;
    letter-spacing: 0px;
    color: #162d43;
  }
  .period_accordion_body_para {
    font-weight: normal;
    font-size: 17px;
    letter-spacing: 0px;
    color: #162d43;
    text-transform: capitalize;
  }
  .period_accordion_label {
    font-weight: normal;
    font-size: 15px;
    letter-spacing: 0px;
    text-transform: capitalize;
    opacity: 1;
    margin-bottom: 9px;
    color: #162d43;
  }
  .period_accordion_input_field {
    font-weight: 500;
    font-size: 17px;
    letter-spacing: 0px;
    color: #162d43;
    opacity: 1;
    width: 100%;
    height: 55px;
    border: 1px solid #add4f3;
    border-radius: 7px;
    padding-left: 18px;
    margin-bottom: 20px;
  }
  .period_accordion_select_field {
    font-weight: 500;
    font-size: 17px;
    letter-spacing: 0px;
    color: #162d43;
    opacity: 1;
    width: 100%;
    height: 55px;
    border: 1px solid #add4f3;
    border-radius: 7px;
    padding-left: 18px;
    margin-bottom: 20px;
    background-color: #ffffff;
  }
}

::placeholder {
  color: #c9c9c9;
  opacity: 1;
  font-weight: normal;
}
